import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import {
  removeFromCart,
  sendingOrder,
  changeField,
  emptyCart
} from '../actions/actionCreators';

export default function Cart() {

  const { cartItems, fields } = useSelector(state => state.serviceSendingToCart);
  const { sendLoading, sendError } = useSelector(state => state.serviceSendingOrder);
  const dispatch = useDispatch();
  const history = useHistory();

  // –æ–±—Ä–∞–±–æ—Ç–∫–∞ –æ—à–∏–±–∫–∏

  if (sendError) {
    history.push("/errorloading");
    return (
      <>
        {console.log('Error with sending order!!!')}
      </>
    )
  }

  // –∫–æ–Ω–µ—Ü –æ–±—Ä–∞–±–æ—Ç–∫–∏ –æ—à–∏–±–∫–∏

  function multiplication(a, b) {
    return a * b;
  }

  const handleDeleteItems = (e, id) => {
    e.preventDefault();
    dispatch(removeFromCart(id));
  }

  const handleChange = evt => {
    const { name, value } = evt.target;
    dispatch(changeField(name, value));
  }

  const handleSubmit = evt => {
    evt.preventDefault();
    sendingOrder(
      dispatch,
      {
        phone: fields.phoneField,
        address: fields.addressField
      },
      cartItems.map(item => {
        return {
          id: item.id,
          price: item.price,
          count: item.quantity
        }
      })
    );
    history.push("/success");
    dispatch(emptyCart());
  }

  return (
    <>
      <main className="container">
        <div className="row">
          <div className="col">

            <section className="cart">
              <h2 className="text-center">–ö–æ—Ä–∑–∏–Ω–∞</h2>
              <table className="table table-bordered">
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">–ù–∞–∑–≤–∞–Ω–∏–µ</th>
                    <th scope="col">–†–∞–∑–º–µ—Ä</th>
                    <th scope="col">–ö–æ–ª-–≤–æ</th>
                    <th scope="col">–°—Ç–æ–∏–º–æ—Å—Ç—å</th>
                    <th scope="col">–ò—Ç–æ–≥–æ</th>
                    <th scope="col">–î–µ–π—Å—Ç–≤–∏—è</th>
                  </tr>
                </thead>
                <tbody>
                  {cartItems.map((item, index) =>
                    <tr key={index}>
                      <th scope="row">{index + 1}</th>
                      <td><Link to={`/catalog/${item.id}`}>{item.name}</Link></td>
                      <td>{item.size}</td>
                      <td>{item.quantity}</td>
                      <td>{item.price}</td>
                      <td>{multiplication(item.price, item.quantity)}</td>
                      <td><button
                        className="btn btn-outline-danger btn-sm"
                        onClick={(e) => handleDeleteItems(e, item.id)}>
                        –£–¥–∞–ª–∏—Ç—å
                    </button></td>
                    </tr>
                  )}
                  <tr>
                    <td colSpan="5" className="text-right">–û–±—â–∞—è —Å—Ç–æ–∏–º–æ—Å—Ç—å</td>
                    <td>
                      {
                        cartItems.length > 0 ? cartItems.map(item => multiplication(item.price, item.quantity))
                          .reduce((sum, current) => sum + current) : null
                      }
                    </td>
                  </tr>
                </tbody>
              </table>
            </section>

            <section className="order">
              <h2 className="text-center">–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑</h2>
              <div className="card" style={{ maxWidth: '30rem', margin: '0 auto' }}>
                <form className="card-body" onSubmit={handleSubmit}>
                  <div className="form-group">
                    <label htmlFor="phone">–¢–µ–ª–µ—Ñ–æ–Ω</label>
                    <input
                      className="form-control"
                      id="phone"
                      name='phoneField'
                      onChange={handleChange}
                      value={fields.phoneField}
                      required="required"
                      placeholder="–í–∞—à —Ç–µ–ª–µ—Ñ–æ–Ω" />
                  </div>
                  <div className="form-group">
                    <label htmlFor="address">–ê–¥—Ä–µ—Å –¥–æ—Å—Ç–∞–≤–∫–∏</label>
                    <input
                      className="form-control"
                      id="address"
                      name='addressField'
                      onChange={handleChange}
                      value={fields.addressField}
                      required="required"
                      placeholder="–ê–¥—Ä–µ—Å –¥–æ—Å—Ç–∞–≤–∫–∏" />
                  </div>
                  <div className="form-group form-check">
                    <input
                      type="checkbox"
                      className="form-check-input"
                      required="required"
                      id="agreement" />
                    <label className="form-check-label" htmlFor="agreement">–°–æ–≥–ª–∞—Å–µ–Ω —Å –ø—Ä–∞–≤–∏–ª–∞–º–∏ –¥–æ—Å—Ç–∞–≤–∫–∏</label>
                  </div>
                  <button type="submit" className="btn btn-outline-secondary">–û—Ñ–æ—Ä–º–∏—Ç—å</button>
                </form>
              </div>
            </section>

          </div>
        </div>
      </main>
      {
        // –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏
        sendLoading ?
          <div className="preloader">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div> : null
        // –∫–æ–Ω–µ—Ü –æ–±—Ä–∞–±–æ—Ç–∫–∏ –∑–∞–≥—Ä—É–∑–∫–∏
      }
    </>
  )
}
