import React, { useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { fetchHits } from '../actions/actionCreators';
import SafeImage from './SafeImage';

export default function Hits() {
  const { hits, loading, error } = useSelector(state => state.serviceHits);
  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    fetchHits(dispatch);
  }, [dispatch]);


  // –æ–±—Ä–∞–±–æ—Ç–∫–∞ –æ—à–∏–±–∫–∏

  if (error) {
    history.push("/errorloading");
    return (
      <>
        {console.log('Error with loading hits!!!')}
      </>
    )
  }

  // –∫–æ–Ω–µ—Ü –æ–±—Ä–∞–±–æ—Ç–∫–∏ –æ—à–∏–±–∫–∏

  return (
    <>
      <main className="container">
        <div className="row">
          <div className="col">
            <section className="top-sales">
              <h2 className="text-center">–•–∏—Ç—ã –ø—Ä–æ–¥–∞–∂!</h2>

              <div className="row">
                {hits.map(o =>
                  <div className="col-4" key={o.id}>
                    <div className="card">

                      <SafeImage
                        src={o.images[0]}
                        className="card-img-top img-fluid"
                        alt={o.title}
                      />

                      <div className="card-body">
                        <p className="card-text">{o.title}</p>
                        <p className="card-text">{o.price} —Ä—É–±.</p>
                        <Link to={`/catalog/${o.id}`} className="btn btn-outline-primary">–ó–∞–∫–∞–∑–∞—Ç—å</Link>
                      </div>
                    </div>
                  </div>
                )}
              </div>

            </section>
          </div>
        </div>
      </main>

      {
        // –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏
        loading ?
          <main className="container">
            <section className="top-sales">
              <h2 className="text-center">–•–∏—Ç—ã –ø—Ä–æ–¥–∞–∂!</h2>
              <div className="preloader">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </div>
            </section>
          </main> : null
        // –∫–æ–Ω–µ—Ü –æ–±—Ä–∞–±–æ—Ç–∫–∏ –∑–∞–≥—Ä—É–∑–∫–∏
      }
    </>
  )
}
